import {
	mapGetters,
	mapActions
} from 'vuex';


export default {

	computed: {
		...mapGetters(['statusBarHeight'])
	},
	data() {
		return {
			trade_pwd: '',
			focus: true,
			show: false,
			numarr: [],
			d_time: 10,
			nextsteep: false,
			tel: 0,
			numlength: "",
			gopen: false,
			isactive: true,
			resgetcode: false,
			boardlists: [{
					id: 1,
					con: 1,
					checked: false
				}, {
					id: 2,
					con: 2,
					checked: false
				}, {
					id: 3,
					con: 3,
					checked: false
				}, {
					id: 4,
					con: 4,
					checked: false
				}, {
					id: 5,
					con: 5,
					checked: false
				}, {
					id: 6,
					con: 6,
					checked: false
				}, {
					id: 7,
					con: 7,
					checked: false
				}, {
					id: 8,
					con: 8,
					checked: false
				}, {
					id: 9,
					con: 9,
					checked: false
				}, {
					id: 10,
					con: "",
					checked: false
				}, {
					id: 11,
					con: 0,
					checked: false
				},
				{
					id: 12,
					con: "x",
					checked: false
				},

			]
		};
	},
	computed: {
		len: function() {
			if (this.trade_pwd.length == 6) {
				// 第一次进来可以点击的

			}
			return this.trade_pwd.length
		}
	},
	onLoad(e) {

	},
	methods: {
		writepwd(num) {
			if (num == 12) {
				this.close();
				return;
			}
			if (num == 10) {
				return;
			}
			if (num == 11) {
				num = 0;
				this.boardlists[10].checked = true;
			} else {
				this.boardlists[num - 1].checked = true;
			}
			this.numarr.push(num);
			if (this.numarr.length > 6) {
				return
			}
			var that = this;
			setTimeout(function() {
				for (var i = 0; i < that.boardlists.length; i++) {
					that.boardlists[i].checked = false;
				}
			}, 200)
			this.trade_pwd = this.numarr.join("");


		},
		//密码框
		back() {
			uni.navigateBack();
		},
		//回退方法
		close() {
			this.numarr.pop();
			if (this.trade_pwd.length > 0) {
				this.trade_pwd = this.trade_pwd.substring(0, this.trade_pwd.length - 1);
			}
		},
		blur1() {
			this.show = false
		},
		focus1() {

			this.show = true
		},

		// 确认执行的方法
		setpwd() {
			if (this.trade_pwd.length < 6) {
				console.log('密码长度不能少于6位');
				T.tips('密码长度不能少于6位')
				return;
			}
			// 密码长度为6位以后执行方法
			console.log(this.trade_pwd);
			uni.navigateBack({
				delta:1
			})
		},

	}

};
